		box-shadow: 0 12px 18px 0 rgba(0, 0, 0, .4), inset 0 1px 0 0 hsla(0, 0%, 100%, .1);
		color: hsla(0, 0%, 100%, .9);
		display: inline-block;
		font-size: 14px;
		line-height: 18px;
		margin: auto;
		max-height: 676px;
		max-width: 300px;
		opacity: 0;
		padding: 18px;
		pointer-events: auto;
		position: absolute;
		text-align: center;
		visibility: hidden
	}

	#toast.toast_top {
		top: 20px
	}

	#toast.toast_bottom {
		bottom: 20px
	}

	#toast.toast_show {
		opacity: .96;
		visibility: visible
	}

	#toast.toast_hide {
		opacity: 0;
		transition: visibility 0s .3s, opacity .3s linear;
		visibility: hidden
	}

	#notification {
		background-color: #30303c;
		border-radius: 3.48px;
		color: hsla(0, 0%, 100%, .9);
		display: flex;
		display: inline-block;
		font-size: 14px;
		line-height: 18px;
		margin: auto;
		max-height: 676px;
		max-width: 300px;
		opacity: 0;
		padding: 18px;
		position: absolute;
		text-align: center;
		visibility: hidden
	}

	#notification.toast_top {
		top: 20px
	}

	#notification.toast_bottom {
		bottom: 20px
	}

	#notification.toast_show {
		opacity: 1;
		visibility: visible
	}

	#notification.toast_hide {
		opacity: 0;
		transition: visibility 0s .3s, opacity .3s linear;
		visibility: hidden
	}

	#notification-icon {
		background-color: #fff;
		border-radius: 3.48px;
		height: 44px;
		width: 44px
	}

	#notification-message {
		font-size: 14px;
		line-height: 18px;
		margin-left: 10px;
		text-align: left
	}

	.footer-text-img {
		transition: .2s;
	}

	img {
		overflow-clip-margin: content-box;
		overflow: clip;
	}

	button .text-port {
		font-size: 12px;
		font-weight: 900;
	}

	#game-header-holder {
		display: none;
	}

	.game-title {
		width: 300px;
		height: 17px;
		position: absolute;
		display: flex;
		color: white;
		opacity: 0.85;
		text-shadow: rgb(65, 50, 24) 1px 0px 0px, rgb(65, 50, 24) 0.552px 0.85px 0px, rgb(65, 50, 24) -0.4px 0.9px 0px, rgb(65, 50, 24) -0.1px 0.15px 0px, rgb(65, 50, 24) -0.65px -0.7px 0px, rgb(65, 50, 24) 0.3px -0.95px 0px, rgb(65, 50, 24) 0.96px -0.28px 0px;
		pointer-events: none;
		white-space: nowrap;
		z-index: 250;
		/* margin-top: 10px; */
	}

	.time_stamp {
		height: 17px;
		position: absolute;
		display: flex;
		color: white;
		opacity: 0.85;
		text-shadow: rgb(65, 50, 24) 1px 0px 0px, rgb(65, 50, 24) 0.552px 0.85px 0px, rgb(65, 50, 24) -0.4px 0.9px 0px, rgb(65, 50, 24) -0.1px 0.15px 0px, rgb(65, 50, 24) -0.65px -0.7px 0px, rgb(65, 50, 24) 0.3px -0.95px 0px, rgb(65, 50, 24) 0.96px -0.28px 0px;
		pointer-events: none;
		font-family: monospace;
		z-index: 250;
		/* margin-top: 10px; */
	}

	.maskAnimate {
		animation: animate .5s ease;
		animation-fill-mode: forwards;
	}

	.upendMaskAnimate {
		animation: upendAnimate .5s ease;
		animation-fill-mode: forwards;
	}

	@keyframes animate {
		from {
			transform: translateY(100%);
			display: none;
		}

		to {
			transform: translateY(0%);
			display: block;
		}
	}

	@keyframes upendAnimate {
		from {
			transform: translateY(0%);
			display: block;
		}

		to {
			transform: translateY(100%);
			display: none;
		}
	}

	.showAnimate {
		animation: leftAnimate .75s ease;
		animation-fill-mode: forwards;
	}


	.hideAnimate {
		animation: rightAnimate .75s ease;
		animation-fill-mode: forwards;
	}



	@keyframes leftAnimate {
		from {
			transform: translateX(100%);
			display: none;
		}

		to {
			transform: translateX(0%);
			display: block;
		}
	}

	@keyframes rightAnimate {
		from {
			transform: translateX(0%);
			display: block;
		}

		to {
			transform: translateX(100%);
			display: none;
		}
	}
</style>

