			<div id="splash" class="screen_color"
				style="visibility: visible; width: 100vw; height: 100vh !important; z-index: 0;">
				<!-- <div id="footer-container" class="footer-container" style="height: 201.52px;">
					<div class="footer-mask-container-port">
						<div class="footer-mask footer-mask-color"
							style="background-image: linear-gradient(180deg, rgba(195, 32, 182, 0), #C320B6)">
