<script>
export default {
	data() {
		return {
			rotatShowStatus: false,
			isCheck: true,
			configurationHeight: "640",
			gameId: 0
		}
	},
	mounted() {
		this.configurationHeight = window.configurationHeight
		window.closeTable = this.closeTable;
	},
	methods: {
		open() {
			this.rotatShowStatus = this.isSevenDaysApart()
			this.isCheck = true
		},

		closeTable() {
			if (this.isCheck == true) {
				const InitialTime = new Date().getTime();
				window.sessionStorage.setItem("InitialTime" + this.gameId, InitialTime);
			}
			this.rotatShowStatus = false
		},

		openHistory() {
			// window.changeHistory(true)
			if (this.isCheck == true) {
				const InitialTime = new Date().getTime();
				window.sessionStorage.setItem("InitialTime" + this.gameId, InitialTime);
			}
			this.rotatShowStatus = false
			var customEvent = new CustomEvent("HistoryEventFromHTML");
			window.dispatchEvent(customEvent);
		},



		checkButton() {
			this.isCheck = !this.isCheck
		},

		isSevenDaysApart() {
			if (localStorage.getItem("gameId") == null) {
				this.gameId = 0
			} else {
				this.gameId = localStorage.getItem("gameId")
			}
			if (window.sessionStorage.getItem("InitialTime" + this.gameId) == null) {
				return true
			}
			const comparisonTime = new Date().getTime();
			const diffInMillis = Math.abs(comparisonTime - window.sessionStorage.getItem("InitialTime" + this.gameId));
			const sevenDaysInMillis = 30 * 24 * 60 * 60 * 1000;
			if (diffInMillis >= sevenDaysInMillis) {
				return true
			} else {
				return false
			}
		}

	}
}
</script>





