import Vue from 'vue'
import App from './App.vue'
import router from "./assets/js/router.js";
import service from '../src/assets/js/http.js'

Vue.prototype.$http = service;

function setFaviconByBrand() {
	const urlParams = new URLSearchParams(window.location.search)
	const brandName = urlParams.get('brandName') || 'kf'
	document.title = `${brandName.toUpperCase()}Game`

	// public 下文件可直接通过路径访问
	const iconPath = `${process.env.VUE_APP_IMG_URL}/${brandName}/favicon.ico`

	let link = document.querySelector("link[rel~='icon']")
	if (!link) {
		link = document.createElement('link')
		link.rel = 'icon'
		document.head.appendChild(link)
	}
	link.href = iconPath + '?v=' + Date.now()
}

setFaviconByBrand()

Vue.config.productionTip = false

import VueScroll from 'vuescroll';
Vue.use(VueScroll);
new Vue({
	router,
	render: h => h(App),
}).$mount('#app')